<template>
<div class="account">
    <!-- 头部开始 -->
    <header style="line-height: 0.5rem">
      <Head>
        <div slot='a' class="he account_he">
          <div class="left">
            <a-Icon type="left" @click="$router.push('/Shou/My')" />
          </div>
          <div class="content">账户信息</div>
        </div>
      </Head>
    </header>
    <!-- 头部结束 -->
    <!-- 头像开始 -->
    <div class="photo" style="height: 1rem;border-top:1px solid gainsboro;margin-top: 0.2rem;line-height: 0.9rem">
      <div class="photo_left">头像</div>
      <div class="photo_right">
          <img data-v-0fc4ab7a="" src="//elm.cangdu.org/img/default.jpg" class="privateImage" v-if="headPhoto==''"/>
          <img :src="'//elm.cangdu.org/img/'+headPhoto" v-else/>
          <!-- <img src="" /> -->
      </div>
      <input type="file" @change="upload" ref="upload">
    </div>
    <!-- 头像结束 -->
    <!-- 用户名开始 -->
    <div class="photo">
      <div class="photo_left">用户名</div>
      <div class="photo_right">
        {{user}}
        <a-Icon type="right" style="margin-left: 0.1rem"/>
      </div>
    </div>
    <!-- 用户名结束 -->
    <!-- 收货地址开始 -->
    <router-link class="photo" tag="div" to="/Redact">
      <div class="photo_left">收货地址</div>
      <div class="photo_right">
        <a-Icon type="right"/>
      </div>
    </router-link>
    <!-- 收货地址结束 -->
    <div class="h">账号绑定</div>
    <!-- 手机开始 -->
    <div class="photo" style="border-top: 1px solid gainsboro">
      <div class="photo_left">手机</div>
      <div class="photo_right">
        <a-Icon type="right" />
      </div>
    </div>
    <!-- 手机结束 -->
    <div class="h">安全设置</div>
    <!-- 登录密码开始 -->
    <router-link tag="div" to="/Reset" class="photo" style="border-top: 1px solid gainsboro">
      <div class="photo_left">登录密码</div>
      <div class="photo_right">
        修改
        <a-Icon type="right" style="margin-left: 0.1rem" />
      </div>
    </router-link>
    <!-- 登录密码结束 -->
    <!-- 退出登录开始 -->
    <input type="button" value="退出登录" @click='logOut()'/>
    <!-- 退出登录结束 -->
</div>
</template>

<script>
import axios from 'axios'  
import { Uploader } from 'vant'
import {Dialog} from 'vant'  
import Head from '@/page/header/Head'    
export default{
    name:'Account',
    components:{Head,[Dialog.Component.name]: Dialog.Component},
    data() {
      return {
        user:'',
        headPhoto:''
      }
    },
    
    beforeMount() {
      this.headPhoto = this.$store.state.login.headPhoto
    },

    methods: {

      upload(){

              // 上传文件
              var id = this.$store.state.login.id
              const userUploadAtt= (File,config)=>axios.post('https://elm.cangdu.org/eus/v1/users/'+id+'/avatar',File,config)
              let input =this.$refs.upload
              //new 一个对象
              let data = new FormData()
              //用FormData.append为表单添加建值对到表单中
              data.append('file',input.files[0])
              console.log(data)
              userUploadAtt(data,{headers:{'Content-Type': 'multipart/form-data'}}).then(res=>{
                  // console.log(res)
                 this.headPhoto = res.data.image_path
                 this.$store.commit('headPhoto',this.headPhoto)
              })
      },


      // 退出登录

      logOut(){
        Dialog.confirm({
          title: '',
          message: '弹窗内容'
        }).then(() => {
          // on confirm
          // 确认
          let obj = {user:'',code:'',password:'',show:false}
          this.$store.commit('out',obj)
          this.$router.push('/Shou/My')
        }).catch(() => {
          // on cancel
          // 取消
        });
      }
    },
    created() {
        if(this.$store.state.login){
            this.user = this.$store.state.login.user
        }

        
    },
}
</script>

<style lang="scss">
.account{
  width: 100%;
  height: 100%;
  background: #F5F5F5;
  .photo{
    width: 100%;
    height:0.7rem;
    border-bottom:1px solid gainsboro;
    background:white;
    line-height:0.6rem;
    padding:0rem 0.2rem;
    font-size:0.2rem;
    position: relative;
    input[type='file']{
      width: 100%;
      height: 100%;
      position: absolute;
      opacity: 0;
    }
    .photo_left{
      float: left;
    }
    .photo_right{
      color: gray;
      float: right;
      img{
        width:0.7rem;
        height:0.7rem;
        border-radius:50%;
        margin-right:0.1rem;
      }
    }
  }
  .h{
    width: 100%;
    height: 0.5rem;
    padding: 0rem 0.2rem;
    font-size: 0.18rem;
    color: gray;
    line-height: 0.5rem;
  }
  input[type='button']{
    width: 90%;
    height: 0.5rem;
    background: #D8584A;
    color: white;
    text-align: center;
    line-height: 0.5rem;
    border: none;
    outline: none;
    font-size: 0.2rem;
    margin-left: 5%;
    border-radius: 0.05rem;
    margin-top: 0.5rem;
  }
  
}
</style>




